.card-block {
  display: flex;
}

.card-block img {
  width: 12vw;
  opacity: 0.5;
}

.card-block img:hover {
  cursor: pointer;
}

.class-block p {
  font-size: 2vw;
  font-weight: 600;
  text-align: center;
  color: black;
  margin-bottom: 1vw;
}

.class-block {
  display: inline-block;
  margin: 0 3.1vw;
}

.list-row {
  display: flex;
  justify-content: center;
  margin-bottom: 2vw;
}

.progress-bar-wrapper {
  display: flex;
  justify-content: center;
  position: relative;
}

.ant-progress.progress-bar {
  width: 80%;
  margin-top: 2vw;
  margin-bottom: 2vw;
  position: relative;
}

.ant-progress.progress-bar .ant-progress-inner {
  height: 2.08vw;
}

.ant-progress.progress-bar .ant-progress-inner .ant-progress-bg {
  height: 2.08vw !important;
}